.header {
  height: 72px;
  display: flex;
  
  width: 100%;
  min-width: 1440px;
  background: #151515;
  position: relative;
} 

  .navbar-links-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    position: relative;
    width: min-content;
    height: 40px;
    left: 0px;
    top: calc(50% - 40px/2);
  }

  .navbar-logo {
    display: flex;
    align-items: center;
    position: relative;
    height: 100%;
    left: 25px;
    top: 0px;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin-right:  40px;
  }


  .navbar-connect {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    padding: 0px;
    padding-right: 25px;
    width: 100%;
  }
  
  .viralcrypto {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 100%;
    padding-bottom: 0.2rem;
  }  
  
  #navbarlogo {
    height: 30px;
  }
  
  .navbar-user {
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: end;
    margin-right: 0.5rem;
  } 

  
  .avatarPic-wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    object-fit: cover;
  }
  
  .avatarPic {
    height: 50px;
    width: 50px;
    border-radius: 50%;
  }

  .safeconnectbutton{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 16px;
    color: white;
    position: static;
    width: 200px;
    height: 44px;    
    background: linear-gradient(152.95deg, #5DED9C 32.2%, #0EBD91 93.13%);
    border-radius: 8px;
    border: none;
    flex: none;
    order: 1;
    flex-grow: 0;
    margin: 0px 10px;
  }
  
  .fas,
  .far {
    font-size: 1.5rem;
    margin: 1rem;
  }
  
  .fas.fa-sign-out-alt {
    margin: 0 0.5rem 0 0.5rem;
    font-size: 1rem;
  }
  
  .dropdown-logout {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    position: absolute;
    width: 100%;
    bottom: 10px;
    left: 0;
  }
  
  .btn1.logout-btn , .btn2.logout-btn {
    background: linear-gradient(152.95deg, #5DED9C 32.2%, #0EBD91 93.13%);
    border-radius: 8px;
    font-family: ProximaNova;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #FDFDFD;
    border: none;
    margin: 5px 10px;
    width: 90%;
    height: 44px;
  }
  
  .btn1.logout-btn {
    background: linear-gradient(152.95deg, darkgrey, rgb(81, 81, 81));
    color: ste;
  }
  
  .nav-menu {
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .nav-item {
    height: 100%;
    border-radius: 5px;
    display: flex;
    align-items: center;
  }
  
  .nav-links {
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 0;
    height: 100%;
    margin: 0 1.5rem;
    font-family: ProximaNova;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: #FDFDFD;
  }
  
  .nav-links:hover {
    color: #55E796;
    margin-bottom: 10px;
    border-bottom: 2px solid rgb(226, 226, 226);
    transition: all 0.2s ease-out;
    transform: scale(1.2);
  }
  
  .showBalance,
  .showBalanceBSC {
    display: flex;
    justify-content: flex-start;
    font-family: ProximaNova;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 24px;
    align-items: center;
    text-decoration: none;
    padding: 0;
    height: 100%;
    width: 200px;
  }
  
  .showBalance {
    color: rgb(58, 228, 103);
  }
  
  .showBalanceBSC {
    color: gold;
  }
  
  .caret {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0px 0 0px;
  }
  
  #header-menuBars{
    height: 20px;
    margin: 20px 10px;
  }
  #header-menuBars:hover{
    transform: scale(1.2);
    cursor: pointer;
  }
  
  
  .eViralLogo {
    height: 42%;
    margin-left: 0.7rem;
  }
  
  .nav-links-mobile {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .nav-links-mobile:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .menu-icon {
    display: none;
    color: #e7e7e7;
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  a {
    max-width: 100%;
    text-decoration: none;
  }

  .dropdown {
    position: absolute;
    top: 66px;
    width: 375px;
    height: 510px;
    transform: translateX(-80%);
    background-color: #151515;
    padding: 15px;
    overflow: hidden;
    font-size: 18px;
    z-index: 999;
    
  }
  
  .dropdown-item {
    color: white;
    min-height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    transition: background var(--speed);
    background-color: #151515;
    padding: 0;
    padding-left: 15px;
    width: 100%;
  }

  .dropdown-item:hover{
    color: white;
    background-color: #32fff82d;
    border-radius: 10px;
  }
  
  .dropdown-item-user {
    color: white;
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: var(--border-radius);
    transition: background var(--speed);
    border-bottom: var(--border);
    font-family: ProximaNova;
    font-size: 18px;
    background-color: #151515;
  }

  #dropDown-text{
    font-family: ProximaNova;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 24px;    
  }
  
  
  #dropdown-username {    
    font-family: ProximaNova-bold;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 24px;
    margin-left: 20px;
    color: #FDFDFD;
  }
  
  .dropdown-right-icon {
    margin-left: auto;
  }
  
  .wallet-balances {
    height: 100%;
    width: 60%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
  }
  
  .eViralLogo-Dropdown {
    display: flex;
    justify-content: center;
    height: 1.75rem;
    width: auto;
    margin: 10px 15px 10px 0;
  }
  
  

  
  @media only screen and (max-width: 600px) {
    .header{
      width: 100%;
      min-width: 375px;
    }
    .navbarlogo{
      height: 25px;
      margin-left: 0;
    }
    .menu-icon {
      display: flex;
      height: 100%;
      justify-content: flex-end;
      align-items: center;
      margin-left: 0.5rem;
    }

    .nav-menu {
      display: none;
    }
    /* .nav-menu.active {
      display: flex;
      height: 7rem;
      width: 100%;
      position: absolute;
      justify-content: space-evenly;
      top: 67px;
      left: 0;
      background: linear-gradient(
        90deg,
        rgb(28, 27, 27) 0%,
        rgb(26, 23, 23) 100%
      );
      border-bottom: var(--border);
    } */
    .navbar-nav {
      max-width: 100%;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 0 1rem;
    }
    .nav-links {
      margin: 1rem;
    }
    .navbar-connect{
      justify-content: flex-end;
      margin-right: 10px;
    }
    .safeconnectbutton{
      width: 150px;
    }

  }
  